import React, {useCallback, useEffect, useState} from 'react'
import { Typography} from '@mui/material'
import FormikStepper from './FormikStepper'
import {StepCheck ,checkSchema} from './steps/StepCheck'
import {StepPersonal, personalSchema} from './steps/StepPersonal'
import {StepAddress, addressSchema} from 'components/steps/StepAddress'
import {StepVPO, vpoSchema} from './steps/StepVPO'
import {StepConfirm, confirmSchema} from './steps/StepConfirm'
import { useGoogleReCaptcha } from 'react-google-recaptcha-v3';
import Swal from 'sweetalert2'

// async function stall(stallTime = 3000) {
//   await new Promise(resolve => setTimeout(resolve, stallTime));
// }

const initialValues = {
  lastName: '',
  firstName: '',
  middleName: '',
  birthday: '',
  inn: '',
  socialStatus: '',
  familySize: '',
  children: '',
  city: '',
  street: '',
  addrNum: '',
  addrCorp: '',
  addrRoom: '',
  factAddress: '',
  vpoNumber: '',
  vpoDate: '',
  phoneNumber: '',
  agree: false,
  checked: false
}

const RegisterForm = () => {

  const { executeRecaptcha } = useGoogleReCaptcha();
  
  const informUser = async () => {
    const skip = localStorage.getItem('skip')
    if(skip) {
      return
    } 
    Swal.fire({
      // icon: 'info',
      titleText: ' ',
      text: '  ,     .            .',
      input: 'checkbox',
      inputValue: 0,
      inputPlaceholder:
        '   ',
      confirmButtonText:
        '',
        allowOutsideClick: false
    }).then(({value: accept, isConfirmed}) => {
      if (accept) localStorage.setItem('skip', true)
    })

  }

  const handleReCaptchaVerify = useCallback(async () => {
    if (!executeRecaptcha) {
      console.log('Execute recaptcha not yet available');
      return;
    }
    // const token = await executeRecaptcha('someAction');
  }, [executeRecaptcha]);

  useEffect(() => {
    handleReCaptchaVerify();
    informUser()
  }, [handleReCaptchaVerify]);

  return (
    <div style={{ marginBottom: '2em' }}>

      <Typography color="inherit" variant="h6" component="div" sx={{ mb: 4 }}>
         
      </Typography>

      <FormikStepper
        initialValues={initialValues}
        // validateOnChange={false}
        // validateOnBlur={false}
        onSubmit={async (values) => {
          console.log(values)
        }}
      >
          <StepCheck label="" validationSchema={checkSchema} isDisabled={(values) => values.checked} />
          <StepPersonal label="..." validationSchema={personalSchema} />
          <StepAddress label="" validationSchema={addressSchema} />
          <StepVPO label="" validationSchema={vpoSchema} />
          <StepConfirm label="" validationSchema={confirmSchema} />
      </FormikStepper>
      
    </div>
  )
}

export default RegisterForm